:root {
    --blue: #007bff;
    --indigo: #6610f2;
    --purple: #6f42c1;
    --pink: #e83e8c;
    --red: #dc3545;
    --orange: #fd7e14;
    --yellow: #ffc107;
    --green: #28a745;
    --teal: #20c997;
    --cyan: #17a2b8;
    --white: #fff;
    --gray: #6c757d;
    --gray-dark: #343a40;
    --primary: #007bff;
    --secondary: #6c757d;
    --success: #28a745;
    --info: #17a2b8;
    --warning: #ffc107;
    --danger: #dc3545;
    --light: #f8f9fa;
    --dark: #343a40;
    --shadowBlue: #4152bbe0;
}

body {
    margin: 0;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #ffffff;
    text-align: left;
    background: url("https://images.unsplash.com/photo-1516223725307-6f76b9ec8742?crop=entropy&cs=tinysrgb&fit=max&fm=jpg&ixid=M3wyNjI5NjF8MHwxfHNlYXJjaHwyN3x8QXVkaW8lMjBQcm9kdWN0aW9ufGVufDB8fHx8MTY4Njg1OTE5MXww&ixlib=rb-4.0.3&q=80&w=1080")
    no-repeat center center fixed;
    background-size: cover;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    background-blend-mode: darken;
    font-family: 'Roboto', sans-serif !important;
    scroll-behavior: smooth;
}
.navbar-container {
  background-color: rgba(17, 17, 17, 1);
  position: fixed;
  width: 100%;
  top: 0;
  margin: 0;
  color: white;
  overflow: hidden;
  padding-top: 0.9rem;
  padding-bottom: 0.9rem;
  font-family: "Roboto", sans-serif;
  z-index: 50;
}

.navbar {
  margin: auto;
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar h1 {
  margin: 0;
  padding: 0;
  top: 0;
  padding-left: 8px;
  padding-right: 8px;
  font-size: 21px;
  background-color: #111;
}

.navbar ul {
  font-size: 18px;
  list-style-type: none;
  padding: 0;
  margin: 0;
  top: 0;
  /* background-color: #111; */
}

.navbar ul li {
  float: left;
  margin: 0 5px;
}

.navbar ul li a {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100px;
  color: white;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
}

.navbar ul li a:hover:not(.active) {
  background-color: #111;
}

.navbar ul li .active {
  /* background-color: #54304f; */
  /* border: 1px solid; */
  /* box-shadow: #2455bd 0px 0px 5px 2px; */
  box-shadow: rgba(150, 82, 137, 0.8) 0px 0px 5px 2px;
}

