.container {
  height: 100vh;
  background-position: 50% 20%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}
.container h1 {
  color: white;
  width: 700px;
  font-size: 50px;
  text-align: center;
}
.container p {
  font-size: 23px;
  color: white;
  width: 700px;
  text-align: center;
}

.home {
  padding: 20px;
  margin-top: 30px;
  background-color: white;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}
.footer {
  margin: 0;
  padding: 1rem;
  width: 100%;
  /* height: 140px; */
  background-color: black;
}
.footer__redes-wrapper {
  display: flex;
  justify-content: center;
  grid-gap: 40px;
  gap: 40px;
  flex-wrap: wrap;
  max-width: 700px;
  margin: 20px auto;
}
.footer__redes-wrapper li {
  display: inline-block;
}
.footer__redes-wrapper li:hover {
  color: #a1a1a1;
}
.footer__redes-wrapper li:hover img {
  /* background-color: #a1a1a1; */
}
.footer__redes-wrapper img {
  margin-right: 10px;
  display: inline-block;
  background-color: white;
  width: 20px;
  height: 20px;
  vertical-align: text-bottom;
}
.footer__link {
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  font-size: rem(18);
}
.footer__texto {
  color: #a1a1a1;
  text-align: center;
}
.separador {
  background-color: #e0e0e0;
  height: 2px;
  max-width: 30px;
  margin: 15px auto 20px;
}
.footer__redes-wrapper li,
img {
  transition: all ease 0.3s;
}

